import React from "react";
import styles from "../Autorisation/Login.module.css";
import { Button } from "@ya.praktikum/react-developer-burger-ui-components";
import { LoginInput } from "./LoginInput";
import { LoginInputPass } from "./LoginInputPass";
import { RecoverLinks } from "./RecoverLinks";

export function LoginPage() {
  return (
    <section className={`${styles.loginContainer}`}>
      <p className="text text_type_main-medium"></p>
      <div className={`${styles.loginInputs}`}>
        <LoginInput placeholder="E-mail" />
        <LoginInputPass placeholder="" />
      </div>
      <Button htmlType="button" type="primary" size="medium" extraClass={styles.buttonExtra}>  </Button>
      <div className={`${styles.recoverLinks}`}>
        <RecoverLinks mainText="- ?" linkText="" />
        <RecoverLinks mainText=" ?" linkText=" " />
      </div>
    </section>
  );
}
