import React from "react";
import styles from "../Autorisation/RecoverLinks.module.css"

export function RecoverLinks({infoText, linkText}) {
    return (
        <>
        <p className="text text_type_main-default text_color_inactive">
            {infoText} <span className={`${styles.link}`} >{linkText}</span></p>
        </>
    )
}